/******************************************************************

p-blog

******************************************************************/
.p-blog{
  padding-top: rem(50);
  padding-bottom: rem(50);
  // background-color: $bgGray;
  @include mq('tab'){
    padding-top: rem(70);
    padding-bottom: rem(70);
  }
}

.p-blog__description{
  margin-top: rem(40);
  font-size: rem(16);
  @include mq('tab'){
    margin-top: rem(50);
  }
}

.p-blog__contents{
  margin-top: rem(40);
  @include mq('tab'){
    margin-top: rem(50);
  }
}

.p-blog__items{
  @include mq('tab'){
    display: flex;
    flex-wrap: wrap;
  }
  
}

.p-blog__item{
  @include mq('tab'){
    width: calc(50% - 20px / 2);
  }
  @include mq('pc'){
    width: calc(100% / 3 - 20px * 2 / 3);
  }
}

// スマホ時のmargin-top
.p-blog__item:not(:first-child){
  margin-top: rem(30);
  @include mq('tab'){
    margin-top: 0;
  }
}

// タブレット時の左列のmargin-right
.p-blog__item:nth-child(odd){
  @include mq('tab'){
    margin-right: rem(20);
  }
  @include mq('pc'){
    margin-right: 0;
  }
}

//タブレット時の3番目以降のmargin-top
.p-blog__item:nth-child(n+3){ //n+3...３番目から最後まで。　※-n+3...最初から3番目まで。
  @include mq('tab'){
    margin-top: rem(40);
  }
  @include mq('pc'){
    margin-top: 0;
  }
}

//　パソコン時の左2列のmargin-right
.p-blog__item:nth-child(3n+1),
.p-blog__item:nth-child(3n+2){
  @include mq('pc'){
    margin-right: rem(20);
  }
}

//　パソコン時の4番目以降margin-top
.p-blog__item:nth-child(n+4){
  @include mq('pc'){
    margin-top: rem(40);
  }
}

.p-blog__btn{
  margin-top: rem(60);
  // text-align: center;
  text-align: right;
  @include mq('tab'){
    margin-top: rem(80);
  }
}
