/***************************************************************

.c-btn 共通のボタン

***************************************************************/
.c-btn {
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
  padding-right: 1.875rem;
  padding-left: 1.875rem;
  display: inline-block;
  width: 15rem;
  font-family: "Oswald", sans-serif;
  font-size: 1.125rem;
  font-weight: normal;
  line-height: 1;
  letter-spacing: 0.05em;
  color: #2f2c2a;
  background-color: #fff;
  border: 0.0625rem solid #2f2c2a;
  border-radius: 1.875rem;
  text-align: left;
  text-transform: uppercase;
  position: relative;
}

@media screen and (min-width: 768px) {
  .c-btn {
    padding-top: 1.25rem;
    padding-bottom: 1.25rem;
    padding-right: 2.5rem;
    padding-left: 2.5rem;
    width: 17.5rem;
    font-size: 1.375rem;
    border-radius: 2.5rem;
  }
}

.c-btn::before {
  content: '';
  display: block;
  width: 0.9375rem;
  height: 0.125rem;
  background-color: #2f2c2a;
  position: absolute;
  top: 50%;
  right: 1.9375rem;
  transform: translateY(-50%);
  transition: all .3s;
}

@media screen and (min-width: 768px) {
  .c-btn::before {
    width: 1.125rem;
    right: 2.5625rem;
  }
}

.c-btn::after {
  content: '';
  display: block;
  width: 0.9375rem;
  height: 0.125rem;
  background-color: #2f2c2a;
  position: absolute;
  top: 50%;
  right: 1.9375rem;
  transform: translateY(-50%) rotate(90deg);
  transition: all .3s;
}

@media screen and (min-width: 768px) {
  .c-btn::after {
    width: 1.125rem;
    right: 2.5625rem;
  }
}

.c-btn:hover::before {
  transform: translateY(-50%) rotate(180deg);
}

.c-btn:hover::after {
  transform: translateY(-50%) rotate(270deg);
}

/******************************************************************

.c-hamburger-btn

******************************************************************/
.c-hamburger-btn {
  position: relative;
  width: 1.875rem;
  height: 1.25rem;
}

.c-hamburger-btn span {
  width: 100%;
  height: 0.125rem;
  background-color: #2f2c2a;
  position: absolute;
  transition: all .2s;
}

.c-hamburger-btn span:nth-of-type(1) {
  top: 0;
  left: 0;
  transform-origin: 100% 0;
  transition-delay: .3s;
}

.c-hamburger-btn span:nth-of-type(2) {
  top: 50%;
  left: 0;
  transform: scaleX(1) translateY(-50%);
  transform-origin: 100% 0;
  transition-delay: .3s;
}

.c-hamburger-btn span:nth-of-type(3) {
  bottom: 0;
  left: 0;
  transform-origin: 100% 0;
  transition-delay: .3s;
}

.c-hamburger-btn::before {
  width: 100%;
  height: 0.125rem;
  background-color: #2f2c2a;
  content: '';
  position: absolute;
  top: 0;
  left: 0.34375rem;
  transition: all .2s;
  transform-origin: 0 0;
  transform: rotate(45deg) scaleX(0);
  transition-delay: 0s;
}

.c-hamburger-btn::after {
  width: 100%;
  height: 0.125rem;
  background-color: #2f2c2a;
  content: '';
  position: absolute;
  top: 0;
  right: 0.34375rem;
  transition: all .2s;
  transform-origin: 100% 0;
  transform: rotate(-45deg) scaleX(0);
  transition-delay: 0s;
}

.c-hamburger-btn.is-open span:nth-of-type(1) {
  transform: scaleX(0);
  transition-delay: 0s;
}

.c-hamburger-btn.is-open span:nth-of-type(2) {
  transform: scaleX(0) translateY(-50%);
  transition-delay: 0s;
}

.c-hamburger-btn.is-open span:nth-of-type(3) {
  transform: scaleX(0);
  transition-delay: 0s;
}

.c-hamburger-btn.is-open::before {
  transform: rotate(45deg) scaleX(1);
  transition-delay: .3s;
}

.c-hamburger-btn.is-open::after {
  transform: rotate(-45deg) scaleX(1);
  transition-delay: .3s;
}

